.project-container {
    width: calc(100% - 60px);
    height: 200px;
    border: 1px solid black;
    background-color: black;
    margin: 10px auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    color: white;
}

.project-info {
    width: 300px;
    height: 125px;
    margin: 0;
    /* padding: 0 25px; */
}

.project-text-title {
    font-weight: 400;
    padding: 0 25px;
}

.project-text {
    padding: 0 25px;
    height: 59px;
}

.project-links-container {
    display: -ms-flexbox;
    display: flex;
    margin: 20px 45px 20px 30px;
    height: 55px;
    width: 100px;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.project-links-container-unavailable {
    display: -ms-flexbox;
    display: flex;
    margin: 20px 45px 20px 30px;
    height: 55px;
    width: 220px;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.project-links-icons {
    width: 40px;
    height: 40px;
    opacity: 1;
    -webkit-transition-duration: 0.2s;
         -o-transition-duration: 0.2s;
            transition-duration: 0.2s;
}

.project-links-icons:hover {
    opacity: 0.6;
    -webkit-transition-duration: 0.2s;
         -o-transition-duration: 0.2s;
            transition-duration: 0.2s;
}

.project-unavailable {
    margin: 0;
    width: 170px;
}

.project-image {
    width: calc(100% - 301px);
    height: 200px;
    margin: 0;
}

.project-description {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    color: transparent;
    width: 100%;
    height: calc(100% - 20px);
    padding-top: 20px;
    /* margin-top: -17px; */
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
    opacity: 0;
}

.project-description:hover {
    color: white;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
    opacity: 0.9;
    background-color: black;
}

.project-description-text {
    font-size: 17px;
    width: 90%;
    /* margin-left: 5%; */
}


