.links-container {
    width: 40px;
    height: 40px;
}

.links-icons {
    height: 30px;
    width: 30px;
    margin: 5px;
    opacity: 1;
    -webkit-transition-duration: 0.2s;
         -o-transition-duration: 0.2s;
            transition-duration: 0.2s;
}

.links-icons:hover {
    opacity: 0.4;
    -webkit-transition-duration: 0.2s;
         -o-transition-duration: 0.2s;
            transition-duration: 0.2s;
}


